
.header {
  position: relative;
  color: #fff;
  overflow: hidden;
  background: rgba(7,17,27,0.5);
}
.header .content-wrapper {
  position: relative;
  padding: 24px 12px 18px 24px;
  font-size: 0;
}
.header .content-wrapper .avatar {
  display: inline-block;
  vertical-align: top;
  border-radius: 4px;
}
.header .content-wrapper .content {
  display: inline-block;
  margin-left: 16px;
  font-size: 14px;
}
.header .content-wrapper .content .title {
  margin: 2px 0 8px 0;
}
.header .content-wrapper .content .title .brand {
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 18px;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
    background-image: url("brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
}
.header .content-wrapper .content .description {
  margin-bottom: 10px;
  line-height: 12px;
  font-size: 12px;
}
.header .content-wrapper .content .support .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.header .content-wrapper .content .support .icon.decrease {
  background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.decrease {
    background-image: url("decrease_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.discount {
  background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.discount {
    background-image: url("discount_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.guarantee {
  background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.guarantee {
    background-image: url("guarantee_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.invoice {
  background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.invoice {
    background-image: url("invoice_1@3x.png");
}
}
.header .content-wrapper .content .support .text {
  font-size: 10px;
  line-height: 10px;
  vertical-align: top;
}
.header .content-wrapper .support-count {
  position: absolute;
  right: 12px;
  bottom: 18px;
  padding: 0 8px;
  height: 24px;
  border-radius: 14px;
  background: rgba(0,0,0,0.2);
  text-align: center;
}
.header .content-wrapper .support-count .count {
  line-height: 24px;
  font-size: 10px;
  vertical-align: top;
}
.header .content-wrapper .support-count .icon-keyboard_arrow_right {
  line-height: 24px;
  font-size: 10px;
  margin-left: 2px;
}
.header .bullentin-wrapper {
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  background: rgba(7,17,27,0.8);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.header .bullentin-wrapper .bullentin-title {
  display: inline-block;
  width: 22px;
  height: 12px;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bullentin-wrapper .bullentin-title {
    background-image: url("bulletin@3x.png");
}
}
.header .bullentin-wrapper .bullentin-text {
  margin: 0 4px;
  height: 12px;
  font-size: 10px;
  vertical-align: top;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  filter: blur(10px);
}
.header .detail {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: auto;
  background: rgba(7,17,27,0.8);
}
.header .detail .detail-wrapper {
  min-height: 100%;
  width: 100%;
}
.header .detail .detail-wrapper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.header .detail .detail-wrapper .detail-main .name {
  line-height: 16px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.header .detail .detail-wrapper .detail-main .star-wrapper {
  margin-top: 8px;
  padding: 2px 0;
  text-align: center;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
}


