
.tabbar {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tabbar::after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.tabbar .tag-item {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  display: block;
  color: #4d555d;
  font-size: 14px;
}
.tabbar .router-link-active {
  color: #f01414;
}


